import { useQuery } from "react-query";
import { fetchCoinTickers } from "../api";
import ApexChart from "react-apexcharts";

interface PriceProps {
  coinId: string;
}
interface PriceData {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  circulating_supply: number;
  total_supply: number;
  max_supply: number;
  beta_value: number;
  first_data_at: string;
  last_updated: string;
  quotes: {
    USD: {
      ath_date: string;
      ath_price: number;
      market_cap: number;
      market_cap_change_24h: number;
      percent_change_1h: number;
      percent_change_1y: number;
      percent_change_6h: number;
      percent_change_7d: number;
      percent_change_12h: number;
      percent_change_15m: number;
      percent_change_24h: number;
      percent_change_30d: number;
      percent_change_30m: number;
      percent_from_price_ath: number;
      price: number;
      volume_24h: number;
      volume_24h_change_24h: number;
    };
  };
}

function Price({ coinId }: PriceProps) {
  const { isLoading, data } = useQuery<PriceData>(["tickers", coinId], () =>
    fetchCoinTickers(coinId)
  );
  const athPrice = data?.quotes.USD.ath_price || 100;
  const curPrice = data?.quotes.USD.price || 0;
  const pricePer = Number(((curPrice / athPrice) * 100).toFixed(3));

  return (
    <div>
      {isLoading ? (
        "Loading chart..."
      ) : (
        <ApexChart
          type="radialBar"
          series={[pricePer]}
          options={{
            theme: {
              mode: "dark",
            },
            chart: {
              height: 700,
            },
            plotOptions: {
              radialBar: {
                hollow: {
                  size: "70%",
                },
              },
            },
            labels: ["Current / Highest"],
          }}
        />
      )}
    </div>
  );
}

export default Price;
