import { useQuery } from "react-query";
import { fetchCoinHistory } from "../api";
import ApexChart from "react-apexcharts";

interface Ihistorical {
  time_open: number;
  time_close: number;
  open: string;
  high: string;
  low: string;
  close: string;
  volume: string;
  market_cap: number;
}

interface ChartProps {
  coinId: string;
}

function Chart({ coinId }: ChartProps) {
  const { isLoading, data } = useQuery<Ihistorical[]>(["ohlcv", coinId], () =>
    fetchCoinHistory(coinId)
  );

  return (
    <div>
      {isLoading ? (
        "Loading chart..."
      ) : (
        <ApexChart
          type="candlestick"
          series={[
            {
              data:
                data?.map((info) => {
                  const res = {
                    x: new Date(info.time_close),
                    y: [
                      Number(info.open),
                      Number(info.high),
                      Number(info.low),
                      Number(info.close),
                    ],
                  };

                  return res;
                }) || [],
            },
          ]}
          options={{
            theme: {
              mode: "dark",
            },
            chart: {
              height: 700,
              width: 500,
              toolbar: {
                show: false,
              },
            },
            xaxis: {
              type: "datetime",
              categories: data?.map((info) => info.time_close),
            },
          }}
        />
      )}
    </div>
  );
}

export default Chart;
